<template>
  <div class="hello">
    <el-alert :title="title" type="success" :closable="false">
    </el-alert>
    <div class="add">
      <el-button type="primary" @click="add">添加</el-button>
    </div>
    <div class="table_content">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="sign" label="标识">
        </el-table-column>
        <el-table-column label="供应商">
          <template slot-scope="scope">
            <el-input placeholder="请输入内容" v-model="scope.row.window"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="充值号码">
          <template slot-scope="scope">
            <el-input placeholder="请输入内容" v-model="scope.row.clazz"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="运营商">
          <template slot-scope="scope">
            <el-input placeholder="请输入内容" v-model="scope.row.employee"></el-input>
          </template>
        </el-table-column>
        <el-table-column prop="position" label="游标">
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="420px">
          <template slot-scope="scope">
            <el-button type="primary" @click="start(scope.row)" size="small" plain :disabled="scope.row.disabled">开启</el-button>
            <el-button type="info" @click="stop(scope.row)" size="small" plain>停止</el-button>
            <el-button type="danger" @click="deleteRow(scope.row)" size="small" plain>删除</el-button>
            <span class="status" v-if="scope.row.status!=''">{{scope.row.status}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Main',
  props: {
  },
  data() {
    return {
      title: '秒单管理',
      tableData: [],
      requestSpeed: ''
    }
  },
  created() {
    // 获取请求频次
    let url = 'http://47.93.7.103:9001/prod-cancelled/aip/frequency'
    axios.get(url).then((res)=>{
      console.log(res);
      if(res.status == 200) {
         this.requestSpeed = parseInt(res.data.msg)
        console.log('this.requestSpeed:', this.requestSpeed)
      }
    })
  },  
  methods: {
    // 添加数据
    add() {
          // console.log('this.generateRandomSign():', this.generateRandomSign());
          let obj = {
            sign: this.generateRandomSign(),
            window: '',
            clazz: '',
            employee: '',
            position: 1,
            timer: null,
            disabled: false,
        status: ''
      }
      this.tableData.push(obj);
    },
    // 删除
    deleteRow(row) {
      if(row.status === '开启') {
        this.$message({
          message: '处于开启状态无法删除',
          type: 'error'
        });
        return
      }
      this.tableData = this.tableData.filter(item => item.sign!=row.sign)
    },
    // 停止
    stop(row) {
      let data = {
        biaoshi: row.sign,
        chuangkou: row.window,
        banci: row.clazz,
        uid: row.employee,
        cursor: row.position++
      }
      // console.log(data)
      let url = 'http://47.93.7.103:9001/prod-cancelled/aip/delete'
      axios.post(url,data).then((res)=>{
        console.log(res);
      })
      this.tableData.map(v => {
        if(v.sign === row.sign) {
          v.position = 1
          v.disabled = false
          v.status = '已停止'
          clearInterval(v.timer)
        }
      })
    },
    // 开启
    start(row) {
      const {window,clazz,employee} = row
      if(!window.trim()) {
        this.$message({
          message: '供应商不能为空',
          type: 'warning'
        });
        return;
      }
      if(!clazz.trim()) {
        this.$message({
          message: '充值号码不能为空',
          type: 'warning'
        });
        return;
      }
      if(!employee.trim()) {
        this.$message({
          message: '运营商不能为空',
          type: 'warning'
        });
        return;
      }
      // 调用开启对应得后端API
      let timer = setInterval(()=>{
        // row
        let data = {
          uid: row.sign,
          supplierId: row.window,
          phoneNo: row.clazz,
          company: row.employee,
          cursor: row.position++
        }
        console.log(data)
        let url = 'http://47.93.7.103:9001/prod-cancelled/aip/add'
        axios.post(url,data).then((res)=>{
          console.log(res);
          // if(res.status == 200) {

          // }
        })

      }, this.requestSpeed)
      
      this.tableData.map(item => {
        if(item.sign === row.sign) {
          item.timer = timer
          item.disabled = true
          item.status = '开启'
        }
      })
    },
    // 生成随机标识
    generateRandomSign() {
      let date = (new Date()).valueOf();//获取时间戳
      let txt = '1234567890';//生成的随机机器码
      let len = 13;//机器码有多少位
      let pwd = '';//定义空变量用来接收机器码
      for (let i = 0; i < len; i++) {
        pwd += txt.charAt(Math.floor(Math.random() * txt.length));//循环机器码位数随机填充
      }
      //相加生成唯一ID
      return date + pwd;
    }
  }
}
</script>

